.detail_wrapper {
    width: 100vw;
    height: 100vh;
    padding: 100px;
    overflow-y: scroll;
    color: white;
}

.detail_title {
    font-size: 42px;
}

.detail_description {
    width: 100%;
}

.detail_description_block {
    width: 100%;
    margin: 20px 0;
    word-break: break-word;
}

.detail_description_block > img {
    width: 100%;
    max-height: 300px;
    object-fit: contain;
}

.detail_data {
    position: absolute;
    right: 100px;
    display: flex;
    align-items: center;
}

.detail_created {
    font-size: 18px;
}

.detail_view {
    display: flex;
    align-items: center;
    font-size: 18px;
    margin-left: 10px;
}

.detail_view > img {
    height: 30px;
}

@media(max-width: 800px) {
    .detail_wrapper {
        padding: 100px 10px;
    }

    .detail_data {
        right: 10px;
    }
}